@import "color.sass";
.advantages {
	h2 {
		@include h2;
		padding-top: 70px; }
	ul {
		padding-bottom: 45px;
		padding-top: 35px;
		padding-left: 30px;
		padding-right: 20px;
		@media(max-width: 767px) {
			padding: 35px 20px 45px 20px; } }
	li {
		position: relative;
		font-size: 16px;
		line-height: 30px;
		padding-left: 30px;
		padding-right: 20px;
		color: $gray;
		span {
			width: 6px;
			height: 6px;
			display: inline-block;
			background: $purple;
			border-radius: 160px;
			position: absolute;
			left: 15px;
			top: 11px; } }
	&__img {
		width: 100%;
		height: 100%;
		position: relative;
		background-size: cover;
		background-image: url("../img/advantages-1.png");
		@media(max-width: 767px) {
			height: 460px; }
		&--img {
			background-image: url("../img/advantages-2.png"); } }
	.col-sm-6 {
		padding: 0; } }
