.form {
  position: absolute;
  left: 0;
  top: 0;
  background: #ffffff;
  width: 100%;
  height: 100vh;
  z-index: 1;
}

.form__block {
  width: 320px;
  position: absolute;
  flex-direction: column;
  left: 0;
  right: 0;
  margin: 0 auto;
  top: 50%;
  transform: translateY(-50%);
}

@media (max-height: 600px) {
  .form__block {
    top: 0;
    transform: translateY(0%);
  }
}

.form__block h4 {
  font-size: 42px;
  color: #555555;
  padding-bottom: 10px;
  padding-top: 20px;
}

.form__block p {
  font-size: 18px;
  color: #333333;
  font-weight: 300;
  padding-bottom: 40px;
}

.form__block input {
  width: 100%;
  height: 50px;
  background: #f6f6f6;
  margin-bottom: 20px;
  font-size: 16px;
  font-weight: 200;
  padding: 15px 20px;
  border-radius: 4px;
  box-sizing: border-box;
}

.form__block input[type='submit'] {
  background: #45279f;
  color: #ffffff;
  border: 2px solid #45279f;
  transition: all 0.3s;
}

.form__block input[type='submit']:hover {
  background: none;
  color: #45279f;
}

.form__block__file {
  height: 50px;
  position: relative;
  border-radius: 4px;
  box-sizing: border-box;
  background: #f6f6f6;
  margin-bottom: 40px;
}

.form__block__file input {
  visibility: hidden;
}

.form__block__file label {
  position: absolute;
  left: 0;
  top: 0;
  padding: 15px 20px;
  font-size: 16px;
  font-weight: 300;
  color: #666666;
  width: 100%;
  height: 100%;
  display: flex;
  cursor: pointer;
}

.form__block__file label i {
  position: absolute;
  right: 20px;
  top: 17px;
  display: inline-block;
  width: 20px;
  height: 20px;
}

.form__block__file label i:hover {
  color: #45279f;
}

.form__block__checkbox {
  flex-direction: column;
  padding-bottom: 40px;
}

.form__block__checkbox input {
  display: none;
}

.form__block__checkbox input + label {
  font-size: 12px;
  font-weight: 300;
  padding-left: 35px;
  position: relative;
  color: #333333;
}

.form__block__checkbox input + label:before {
  content: '';
  display: inline-block;
  position: absolute;
  left: 0;
  top: 0;
  width: 20px;
  height: 20px;
  border: 2px solid #333333;
}

.form__block__checkbox input + label a {
  color: #333333;
  border-bottom: 1px solid #000000;
}

.form__block__checkbox input + label:hover a {
  border-bottom: 1px solid #45279f;
  color: #45279f;
}

.form__block__checkbox input:checked + label:before {
  background: #45279f;
  content: '\e905';
  color: #ffffff;
  padding-left: 3px;
  padding-top: 2px;
  font-family: "icomoon";
}

.form__close {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 40px;
  height: 40px;
  cursor: pointer;
}

.form__close:hover span:after, .form__close:hover span:before {
  background-color: #45279f;
}

.form__close span {
  background-color: transparent;
  position: relative;
  width: 40px;
  height: 40px;
  display: inline-block;
}

.form__close span:after, .form__close span:before {
  content: "";
  position: absolute;
  left: 0;
  top: 10px;
}

.form__close span:after {
  content: '';
  top: 30px;
  width: 36px;
  height: 2px;
  background-color: #333333;
  transition: all 0.3s;
  backface-visibility: hidden;
  border-radius: 2px;
  transform: rotate(-45deg) translate(9px, -8px);
}

.form__close span:before {
  content: '';
  transform: rotate(45deg) translate(6px, 5px);
  width: 36px;
  height: 2px;
  background-color: #333333;
  transition: all 0.3s;
  backface-visibility: hidden;
  border-radius: 2px;
}

