@import "color.sass";
.slideout-menu {
  position: fixed;
  z-index: 8;
  top: 0;
  bottom: 0;
  display: none;
  overflow-y: scroll;
  width: 256px;
  min-height: 100vh;
  -webkit-overflow-scrolling: touch;
  background: #181a22;
  // хром, сафари
  &::-webkit-scrollbar {
    width: 0; }
  -ms-overflow-style: none;
  overflow: -moz-scrollbars-none; }
.slideout-menu-right {
  right: 0;
  overflow-x: hidden;
  ul {
    list-style: none;
    padding-left: 10px;
    padding-top: 55px;
    position: relative; }
  li {
    padding-top: 35px;
    padding-left: 40px; }
  a {
    color: #cdcdcd;
    font-size: 20px;
    font-weight: 100;
    transition: all 0.3s; }
  .user {
    ul {
      padding-top: 5px; } } }
.slideout-menu-left {
  left: 0; }
.slideout-panel {
  position: relative;
  z-index: 9;
  min-height: 100vh;
  background-color: #fff;
  will-change: transform;
  transition: all ease 0.89s;
  &.is-active {
    transform: translateX(110%);
    position: fixed; } }
.slideout-open {
  overflow: hidden; }
.slideout-open body,
.slideout-open .slideout-panel {
  overflow: hidden; }
.slideout-open .slideout-menu {
  display: block; }
.panel:before {
  content: '';
  display: block;
  background-color: rgba(0, 0, 0, 0);
  transition: background-color 0.5s ease-in-out; }
.panel-open:before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 999; }

