.menu-close {
  position: absolute;
  top: 20px;
  right: 10%;
  width: 40px;
  height: 40px;
  cursor: pointer;
  span {
    background-color: transparent;
    position: relative;
    top: 20px;
    &:after, &:before {
      content: "";
      position: absolute;
      left: 0;
      top: -10px; }
    &:after {
      content: '';
      top: 10px;
      width: 36px;
      height: 5px;
      background-color: $gray;
      transition: all 0.3s;
      backface-visibility: hidden;
      border-radius: 2px;
      transform: rotate(-45deg) translate(9px, -8px); }
    &:before {
      content: '';
      transform: rotate(45deg) translate(6px, 5px);
      width: 36px;
      height: 5px;
      background-color: $gray;
      transition: all 0.3s;
      backface-visibility: hidden;
      border-radius: 2px; } } }
.top-line-anchor {
  display: inline-block;
  @media(max-width: 767px) {
    left: 20px; } }
.top-line-anchor {
  display: none;
  position: absolute;
  left: 10px;
  top: 10px; }
