@import "color.sass";
.trust {
	position: relative;
	background: #fafafa;
	h2 {
		@include h2;
		padding-top: 80px; }
	.swiper-container {
		width: 100%;
		height: auto;
		margin-left: auto;
		margin-right: auto;
		padding: 50px 35px;
		padding-bottom: 100px;
		@media(max-width: 767px) {
			padding: 50px 0; } }
	.swiper-slide {
		display: flex;
		justify-content: center;
		align-items: center;
		position: relative;
		img {
			position: relative;
			z-index: 9; }
		&:hover {
			.swiper-slide__block {
				display: block;
				&--top {
					display: block; } }
			@media(max-width: 767px) {
				.swiper-slide__block {
					display: none;
					&--top {
						display: none; } } } }
		&__block {
			display: none;
			position: absolute;
			top: 125px;
			background: $purple;
			z-index: 10;
			border-radius: 4px;
			padding: 15px;
			width: 230px;
			p {
				font-size: 14px;
				line-height: 18px;
				color: $white;
				font-weight: 200;
				&:first-child {
					font-size: 20px;
					font-weight: 500;
					line-height: 35px; } }
			&:after {
				content: '';
				position: absolute;
				top: -20px;
				left: 45%;
				border: 10px solid transparent;
				border-bottom: 10px solid $purple; } }
		&__block--top {
			display: none;
			position: absolute;
			background: $purple;
			z-index: 10;
			border-radius: 4px;
			padding: 15px;
			width: 230px;
			bottom: 120px;
			p {
				font-size: 14px;
				line-height: 18px;
				color: $white;
				font-weight: 200;
				&:first-child {
					font-size: 20px;
					font-weight: 500;
					line-height: 30px; } }
			&:after {
				content: '';
				position: absolute;
				left: 45%;
				bottom: -20px;
				border: 10px solid transparent;
				border-top: 10px solid $purple; } } }
	.swiper-container-android .swiper-slide, .swiper-wrapper {
		transform: none; }
	.swiper-button-next {
		background: none;
		right: 60px;
		top: 57%;
		img {
			width: 35px;
			height: 60px;
			transform: rotate(180deg); }
		@media(max-width: 1024px) {
			display: none; } }
	.swiper-button-prev {
		background: none;
		left: 30px;
		top: 57%;
		img {
			width: 35px;
			height: 60px; }
		@media(max-width: 1024px) {
			display: none; } } }













